#dashboardTable [data-test='tab-bar'] {
    margin-bottom: 15px;

    button {
        min-width: 115px;
    }
}

[data-input-id='overview_input'] {
    grid-column: 5 / 5;
    align-content: flex-end;
    grid-row: 3;
    place-self: end;
    [data-test='input-container'] {
        display: inline-flex;
        padding: 0;
    }
    [data-test='input-title-remove-container'] {
        margin: 5px;
    }
    [data-test='input-title'] {
        font-size: 14px;
    }
}

#overview_main_label_viz {
    grid-column: 1 / 4;
    grid-row: 3;
    height: fit-content;
}

#main_page_label_viz {
    grid-column: 1 / span 4;
    grid-row: 1;
    height: fit-content;
}

#main_page_description_viz {
    grid-column: 1 / span 4;
    grid-row: 1;
    height: fit-content;
    margin-top: 40px;
}

#overview_timerange_label_start_viz {
    grid-column: 1;
    grid-row: 3;
    inline-size: fit-content;
    width: 145px;
    margin-top: 28px;

    [data-test='majorValue'] {
        font-size: 14px;
        line-height: 14px;
        font-family: 'Splunk Platform Sans', 'Splunk Platform Sans', 'Proxima Nova', Roboto, Droid,
            'Helvetica Neue', Helvetica, Arial, sans-serif;
        font-weight: 400;
        color: rgb(108, 118, 133);
    }
    [data-test='action-menu-wrapper'] {
        display: none;
    }
}

#overview_timerange_label_end_viz {
    grid-column: 1;
    grid-row: 3;
    inline-size: fit-content;
    margin-left: 145px;
    width: 145px;
    margin-top: 28px;
    display: flex;

    [data-test='majorValue'] {
        font-size: 14px;
        line-height: 14px;
        font-family: 'Splunk Platform Sans', 'Splunk Platform Sans', 'Proxima Nova', Roboto, Droid,
            'Helvetica Neue', Helvetica, Arial, sans-serif;
        font-weight: 400;
        color: rgb(108, 118, 133);
    }
    [data-test='action-menu-wrapper'] {
        display: none;
    }
}
#overview_timerange_label_end_viz::before {
    content: '- ';
    margin: auto;
    color: rgb(108, 118, 133);
}

/* data ingestion */

[data-input-id='data_ingestion_input'] {
    grid-column: 7 / 8;
    align-content: flex-end;
    grid-row: 2;
    place-self: end;
    [data-test='input-container'] {
        display: inline-flex;
        padding: 0;
    }
    [data-test='input-title-remove-container'] {
        margin: 5px;
    }
    [data-test='input-title'] {
        font-size: 14px;
    }
}

#data_ingestion_label_viz {
    grid-column: 1 / span 4;
    grid-row: 1;
    height: fit-content;
}

#data_ingestion_description_viz {
    grid-column: 1 / span 4;
    grid-row: 1;
    height: fit-content;
    margin-top: 32px;
}

#data_ingestion_timerange_label_start_viz {
    grid-column: 1;
    grid-row: 2;
    inline-size: fit-content;
    width: 145px;
    margin-top: 12px;

    [data-test='majorValue'] {
        font-size: 14px;
        line-height: 14px;
        font-family: 'Splunk Platform Sans', 'Splunk Platform Sans', 'Proxima Nova', Roboto, Droid,
            'Helvetica Neue', Helvetica, Arial, sans-serif;
        font-weight: 400;
        color: rgb(108, 118, 133);
    }
    [data-test='action-menu-wrapper'] {
        display: none;
    }
}

#data_ingestion_timerange_label_end_viz {
    grid-column: 1;
    grid-row: 2;
    inline-size: fit-content;
    margin-left: 145px;
    width: 145px;
    display: flex;
    margin-top: 12px;

    [data-test='majorValue'] {
        font-size: 14px;
        line-height: 14px;
        font-family: 'Splunk Platform Sans', 'Splunk Platform Sans', 'Proxima Nova', Roboto, Droid,
            'Helvetica Neue', Helvetica, Arial, sans-serif;
        font-weight: 400;
        color: rgb(108, 118, 133);
    }
    [data-test='action-menu-wrapper'] {
        display: none;
    }
}

#data_ingestion_timerange_label_end_viz::before {
    content: '- ';
    margin: auto;
    color: rgb(108, 118, 133);
}

#data_ingestion_table_viz {
    grid-column: 1 / 8;
    height: 100%;
    max-height: 500px;
    grid-row: 6;
    margin-top: 100px;

    [data-test='viz-waiting-for-input'] {
        min-height: 100px;
    }
}

#data_ingestion_data_volume_viz {
    grid-row: 3 / 6;
    margin-top: 20px;
}

#data_ingestion_events_count_viz {
    grid-row: 3 / 6;
    margin-top: 20px;
}

#data_ingestion_search {
    grid-row: 6;
    width: 150px;
    grid-column: 1;
    margin-top: 16px;
}

#data_ingestion_search_label {
    margin-bottom: 4px;
}

[data-input-id='data_ingestion_table_input'] {
    width: 200px;
    margin-left: 150px;
    grid-row: 6;
    grid-column: 1;
    margin-top: 21px;
    [data-test='input-title'] {
        font-size: 14px;
    }
}

#switch_hide_no_traffic_wrapper {
    grid-column: 1;
    grid-row: 6;
    width: 200px;
    margin: 32px 350px;
}

/* error tab */

[data-input-id='errors_tab_input'] {
    grid-column: 5;
    align-content: flex-end;
    grid-row: 2;
    place-self: end;
    [data-test='input-container'] {
        display: inline-flex;
        padding: 0;
    }
    [data-test='input-title-remove-container'] {
        margin: 5px;
    }
    [data-test='input-title'] {
        font-size: 14px;
    }
}

#errors_tab_description_viz {
    grid-column: 1 / span 4;
    grid-row: 1;
    height: fit-content;
    margin-top: 32px;
}

#errors_tab_timerange_label_start_viz {
    grid-column: 1;
    grid-row: 2;
    inline-size: fit-content;
    width: 145px;
    margin-top: 12px;

    [data-test='majorValue'] {
        font-size: 14px;
        line-height: 14px;
        font-family: 'Splunk Platform Sans', 'Splunk Platform Sans', 'Proxima Nova', Roboto, Droid,
            'Helvetica Neue', Helvetica, Arial, sans-serif;
        font-weight: 400;
        color: rgb(108, 118, 133);
    }
    [data-test='action-menu-wrapper'] {
        display: none;
    }
}

#errors_tab_timerange_label_end_viz {
    grid-column: 1;
    grid-row: 2;
    inline-size: fit-content;
    margin-left: 145px;
    width: 145px;
    display: flex;
    margin-top: 12px;

    [data-test='majorValue'] {
        font-size: 14px;
        line-height: 14px;
        font-family: 'Splunk Platform Sans', 'Splunk Platform Sans', 'Proxima Nova', Roboto, Droid,
            'Helvetica Neue', Helvetica, Arial, sans-serif;
        font-weight: 400;
        color: rgb(108, 118, 133);
    }
    [data-test='action-menu-wrapper'] {
        display: none;
    }
}

#errors_tab_timerange_label_end_viz::before {
    content: '- ';
    margin: auto;
    color: rgb(108, 118, 133);
}

[data-test='status-icon-container'] {
    z-index: 1;
}

.invisible_before_moving {
    display: none;
}

#errors_tab_errors_list_viz {
    height: 600px;
    grid-row: 7 / 7;
}

#errors_tab_errors_count_viz {
    grid-row: 3 / 6;
    margin-top: 16px;
}
